<script>

export default {
  props: ['patientVisit', 'invoice', 'prescriptions', 'stores', 'items'],

  data () {
    return {
      // prescriptions: [],
      prescriptionsField: [
        {
          key: 'prescription_date',
          label: 'DATE',
          formatter: this.$options.filters.date_DATE_MONTH_YEAR,
        },
        { key: 'item_name', label: 'DRUG' },
        // {
        //     key: 'prescription_quantity',
        //     label: 'QTY',
        //     variant: 'success',
        //     tdClass: 'text-center',
        //     thClass: 'text-center'
        // },
        // {
        //     key: 'prescription_issued_qty',
        //     label: 'ISSUED',
        //     variant: 'warning',
        //     tdClass: 'text-center',
        //     thClass: 'text-center'
        // },
        // {key: 'toIssue', label: 'TO ISSUE', variant: 'danger', tdClass: 'text-center', thClass: 'text-center'},
        { key: 'prescription_dosage', label: 'DOSAGE', tdClass: 'text-center', thClass: 'text-center' },
        { key: 'prescription_frequency', label: 'FREQUENCY', tdClass: 'text-center', thClass: 'text-center' },
        {
          key: 'prescription_duration',
          label: 'DURATION (DAYS)',
          tdClass: 'text-center',
          thClass: 'text-center',
        },
        { key: 'action', label: '', tdClass: 'text-center' },
      ],

      selectedPrescription: null,

      dispensingForm: new window.Form({
        ledger_id: this.invoice.id,
        store_id: null,
        item_id: null,
        vendor_id: null,
        date: new Date().toISOString().slice(0, 10),
        quantity: 1,
        unit_price: 0,
        prescription_id: '',
        comments: '',
        items: [],
      }),

      availableStock: 0,

    }
  },

  computed: {},

  watch: {
    'dispensingForm.store_id': function (storeId) {
      if (storeId) {
        this.$httpClient.get('/available_stock/' + this.selectedPrescription.item_id + '/' + storeId)
          .then(({ data }) => {
            this.availableStock = data
          }).catch(error => {
            console.log(error)
          })
      }
    },
    'dispensingForm.item_id': function (itemId) {
      if (itemId) {
        this.findProductPriceSetup(itemId)
      }
    },
  },

  methods: {
    setupStoreAndPrice () {
      const store = this.stores.find((store) => store.name === 'Pharmacy')
      this.dispensingForm.store_id = store.id
    },

    showDispensingForm (prescription) {
      this.reset()

      this.selectedPrescription = prescription

      this.dispensingForm.prescription_id = prescription.prescription_id
      this.dispensingForm.item_id = prescription.item_id
      this.dispensingForm.quantity = parseInt(prescription.prescription_quantity) + parseInt(prescription.prescription_issued_qty)

      this.$bvModal.show('dispensing-modal')
    },

    dispense () {
      const vm = this

      const item = {
        description: this.items.find((item) => item.id === vm.dispensingForm.item_id).name,
        item_id: this.dispensingForm.item_id,
        quantity: this.dispensingForm.quantity,
        unit_price: this.dispensingForm.unit_price,
        ledger_id: this.dispensingForm.ledger_id,
        store_id: this.dispensingForm.store_id,
        prescription_id: this.dispensingForm.prescription_id,
      }

      this.dispensingForm.items.push(item)

      this.dispensingForm.post('/patient-bills')
        .then(() => {
          this.$toast.success('Prescription has been dispensed')

          this.$bvModal.hide('dispensing-modal')

          this.$emit('bill-added')
        })
        .catch()
    },

    findProductPriceSetup (itemId) {
      const item = this.items.find((item) => item.id === itemId)

      if (this.invoice.wallet.type === 0) {
        this.dispensingForm.unit_price = item.cash_price.charge_value * 0.01
      }

      if (this.invoice.wallet.type === 1) {
        this.dispensingForm.unit_price = item.scheme_price.charge_value * 0.01
      }
    },

    reset () {
      this.dispensingForm.store_id = ''
      this.availableStock = 0
      this.selectedPrescription = ''
      this.dispensingForm.prescription_id = ''
      this.dispensingForm.item_id = ''
      this.dispensingForm.quantity = 0
    },
  },
}
</script>
<template>
  <div>
    <b-modal
      id="prescriptions-modal"
      title="Prescriptions"
      no-close-on-backdrop
      footer-class="d-flex justify-content-between bg-muted-lt border-top"
      content-class="bg-white rounded-3"
      modal-class="modal-blur"
      body-class="px-4"
      hide-header-close
      no-close-on-esc
      no-enforce-focus
      size="lg"
    >
      <div class="row row-cards">
        <div class="space-y">
          <div v-for="prescription in prescriptions" :key="prescription.prescription_id" class="card">
            <div class="row g-0">
              <div class="col">
                <div class="card-body">
                  <div class="row">
                    <div class="col">
                      <h3 class="mb-0">
                        <a href="#" @click.prevent="showDispensingForm(prescription)">
                          {{ prescription.item_name }}</a>
                      </h3>
                    </div>
                  </div>
                  <div class="row">
                    <div class="col-md">
                      <div
                        class="mt-2 list-inline list-inline-dots mb-0 text-muted d-sm-block d-none"
                      >
                        <div class="list-inline-item">
                          <i class="uil uil-prescription-bottle text-indigo" />
                          {{ prescription.prescription_dosage }}
                        </div>
                        <div class="list-inline-item">
                          <i class="uil uil-clock text-teal" />
                          {{ prescription.prescription_frequency }} times
                        </div>
                        <div class="list-inline-item">
                          <i class="uil uil-calender" />
                          {{ prescription.prescription_duration }} days
                        </div>
                      </div>
                    </div>
                    <div class="col-md-auto">
                      <div class="mt-3 badges">
                        <a
                          href="#"
                          class="badge badge-outline text-muted border fw-normal badge-pill"
                          @click="showDispensingForm(prescription)"
                        >Dispense</a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <template #modal-footer="{cancel}">
        <button class="btn" @click="cancel">
          Close
        </button>
      </template>
    </b-modal>

    <b-modal
      id="dispensing-modal"
      hide-header-close
      title="Dispense Prescription"
      no-close-on-backdrop
      content-class="rounded-3 bg-white"
      footer-class="d-flex justify-content-end border-top bg-muted-lt pt-2"
      size="lg"
      @shown="setupStoreAndPrice"
      @hidden="reset"
    >
      <div v-if="selectedPrescription">
        <div class="form-group mb-4">
          <label for="" class="form-label">Drug</label>
          <input
            v-model="selectedPrescription.item_name"
            type="text"
            class="form-control bg-muted-lt"
            disabled
          >
        </div>

        <div class="form-group mb-4">
          <label for="item_id" class="form-label">Price</label>
          <medic-money
            disabled
            :value="dispensingForm.unit_price"
            @input="(value) => {
              dispensingForm.unit_price = value
            }"
          />
        </div>

        <div class="form-group mb-4">
          <label for="" class="form-label">Dispensing Store</label>
          <v-select
            id="store_id"
            v-model="dispensingForm.store_id"
            :options="stores"
            label="name"
            :reduce="(option) => option.id"
            :clearable="false"
          />

          <span class="small text-indigo">Available Stock: {{ availableStock }} Units</span>
          <br>

          <span
            v-if="dispensingForm.hasError('store_id')"
            class="text-danger"
            v-text="dispensingForm.getError('store_id')"
          />
        </div>

        <div class="form-group mb-4">
          <label for="" class="form-label">Quantity to Dispense</label>
          <input v-model="dispensingForm.quantity" type="number" class="form-control">

          <span
            v-if="dispensingForm.errors.has('quantity')"
            class="text-danger"
            v-text="dispensingForm.getError('quantity')"
          />
        </div>

        <p class="card-subtitle">
          The doctor has prescribed {{ selectedPrescription.prescription_dosage }}
          to be taken <span
            class="text-indigo"
          > {{ selectedPrescription.prescription_frequency }} times </span>
          a day for <span class="text-indigo"> {{ selectedPrescription.prescription_duration }} day(s)</span>
        </p>
      </div>

      <template #modal-footer="{cancel}">
        <button
          :disabled="dispensingForm.processing"
          class="btn btn-light me-2"
          @click.prevent="cancel"
        >
          Close
        </button>

        <button
          :disabled="dispensingForm.processing"
          class="btn btn-primary"
          @click.prevent="dispense"
        >
          Dispense
        </button>

        <!--                <div class="my-2">-->
        <!--                    <span class="form-hint">Remember to refresh the browser to reflect the dispensed drugs</span>-->
        <!--                </div>-->
      </template>
    </b-modal>
  </div>
</template>
